.product-detail{
  padding: 3.625em 0 4.875em;
  .m-tabs{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__tab{
    height: 2.75em;
    @include center;
    color: $gray2;
    border: 1px solid transparent;
    border-bottom: none;
    border-radius: .3125em .3125em 0 0;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      color: $black;
    }
    &._active{
      pointer-events: none;
      font-weight: 700;
      color: $black;
      border-color: $gray2;
    }
  }
}

.news2{
  padding: 0 0 6em;
}

.use-table,
.oem-table{
  border: $border;
  border-radius: 0 0 .3125em .3125em;
  padding: 1em 1.875em;
  table{
    width: 100%;
  }
  td{
    border-bottom: $border;
    height: 3.75em;
  }
  tr:last-child{
    td{
      border-bottom: none;
    }
  }
  &__td{
    font-size: 1.125em;
    line-height: 120%;
  }
}

.oem-table{
  td{
    width: 50%;
  }
}

.use-table{
  font-weight: 500;
  td{
    text-align: left;
    &:first-child{

    }
    &:nth-child(1),
    &:nth-child(2){
      width: 25%;
    }
    &:nth-child(3){
      width: 50%;
    }
  }
}


@media screen and (max-width: 992px){
  .product-detail{
    padding: 2.44em 0 6.44em;
    &__tab{
      height: 2.5em;
      span{
        font-size: .875em;
      }
    }
  }

  .use-table,
  .oem-table{
    border-radius: 0;
    padding: .5em 1em;
    td{
      height: 2.5em;
    }
    &__td{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .use-table{
    width: calc(100% + 3.75em);
    margin-left: -1.875em;
    overflow-x: auto;
    table{
      min-width: 35em;
    }
  }
}