.header{
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 10.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-top{
  padding: .25em 0;
  background: #ebebeb;
  &__inner{
    display: flex;
    justify-content: flex-end;
  }
}

.lang{
  display: flex;
  align-items: center;
  &__item{
    margin-left: 1.25em;
    font-size: .75em;
    line-height: 125%;
    text-transform: uppercase;
    font-weight: 600;
    color: #929292;
    &._active{
      color: $black;
      pointer-events: none;
    }
  }
}

.header-middle{
  margin-top: 2em;
}

.burger{
  display: none;
}

.m-menu{
  .lang{
    display: none;
  }
}

.m-nav{
  &__link{
    font-size: 1em;
    line-height: 125%;
    font-weight: 600;
    margin-right: 5.375em;
    &:last-child{
      margin: 0;
    }
  }
}

.nav-catalog{
  margin-right: 5.375em;
  position: relative;
  &__show{
    font-weight: 600;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 1.0625em;
      margin-left: .5em;
    }
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
  &__group{
    margin-bottom: .25em;
    &-title{
      font-weight: 600;
      font-size: .875em;
      line-height: 143%;
    }
    &-hidden{
      padding-top: .25em;
      padding-bottom: .375em;
      padding-left: .625em;
      a{
        font-size: .875em;
        line-height: 157%;
      }
    }
  }
  @media screen and (min-width: 993px){
    &__group{
      &-btn{
        display: none;
      }
      &-hidden{
        display: block !important;
      }
    }
    &__show{
      position: relative;
      z-index: 5;
    }
    &__hidden{
      position: absolute;
      left: -1.5em;
      top: -1em;
      z-index: 4;
      white-space: nowrap;
      min-width: calc(100% + 3em);
      padding: 3em 1.5em 1.5em;
      border: 1px solid #929292;
      border-radius: .3125em;
      background-color: $white;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &:hover{
      .nav-catalog__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.header-auto{
  position: relative;
  &._open{
    .header-auto__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    padding: 0 .75em;
    height: 2.25em;
    border-radius: .3125em;
    border: 1px solid $blue;
    cursor: pointer;
  }
  &__img{
    margin-right: .625em;
    width: 1.3125em;
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: 0;
    width: 14.125em;
    background-color: $white;
    border-radius: .3125em;
    border: 1px solid $blue;
    padding: 0 .75em 1.75em;
    z-index: 5;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__title{
    color: $blue;
    font-weight: 600;
    font-size: 1em;
    line-height: 125%;
  }
  &__top{
    height: 2.25em;
  }
  &__close{
    margin-left: auto;
    width: 1.125em;
    @include center;
    img{
      width: 100%;
    }
  }
  &__form{
    margin: 1em auto 0;
    width: 9.25em;
    max-width: 100%;
  }
  &__grid{
    @include grid100gap(.75em);
  }
  &__btn{
    width: 100%;
    margin-top: 1.19em;
  }
}

.header-bottom{
  margin-top: 1.625em;
}

.header-search{
  display: flex;
  position: relative;
  &._show-variants{
    .header-search__variants{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__field{
    width: 38.5em;
    input{
      width: 100%;
      height: 2.625em;
      font-size: 1em;
      font-family: $font1;
      padding: 0 1em;
      border-radius: 0.3125em;
      border: $border;
      background: $white;
      color: $black;
      &::placeholder{
        color: $gray2;
      }
    }
  }
  &__btn{
    margin-left: .69em;
  }
  &__variants{
    position: absolute;
    top: 100%;
    left: 0;
    width: 38.5em;
    margin-top: .3125em;
    border: $border;
    border-radius: .3125em;
    padding: .25em 1em;
    background-color: $white;
    z-index: 5;
    color: $gray2;
    max-height: 30em;
    overflow-y: auto;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__variant{
    font-size: 1em;
    line-height: 150%;
    min-height: 2.5em;
    display: flex;
    align-items: center;
    span{
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 1.5em;
    position: relative;
    &__logo{
      width: 10.375em;
      position: relative;
      z-index: 7;
    }
    &._open{
      .m-menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .header-top{
    display: none;
  }
  
  .header-middle{
    margin-top: 0;
  }

  .m-menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: .625em;
    left: 1em;
    width: calc(100% - 2em);
    padding: 4.75em 4em 1.5em;
    background-color: $white;
    border: 1px solid $black;
    border-radius: .3125em;
    z-index: 6;
    .lang{
      display: flex;
      margin-top: 2em;
    }
  }

  .lang{
    &__item{
      font-size: 1em;
      line-height: 125%;
      margin: 0 1.6875em 0 0;
    }
  }

  .m-nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      line-height: 137%;
      margin: 0 0 .5em;
    }
  }

  .nav-catalog{
    margin: 0 0 2.25em;
    &__show{
      display: none;
    }
    &__group{
      &._open{
        .nav-catalog__group-btn{
          transform: rotate(-180deg);
        }
      }
      &-show{
        display: flex;
        align-items: center;
      }
      &-title{
        font-size: 1em;
        line-height: 137%;
      }
      &-btn{
        width: 1em;
        @include center;
        margin-left: .5em;
        img{
          width: 100%;
        }
      }
      &-hidden{
        padding-left: 1em;
        display: none;
        a{
          font-size: 1em;
          line-height: 137%;
        }
      }
    }
  }

  .burger{
    width: 2.375em;
    position: relative;
    display: block;
    z-index: 12;
    &__btn{
      transition: all .3s;
      @include centerAbsolute;
      width: 100%;
      span{
        display: block;
        width: 100%;
        height: .4375em;
        background-color: $black;
        margin: 0 0 .25em;
        &:last-child{
          margin-bottom: 0;
        }
      }
      img{
        width: 100%;
        display: block;
      }
    }
    ._view2{
      width: 1.375em;
      opacity: 0;
    }
  }

  .header-bottom{
    margin-top: 1.38em;
    &__inner{
      padding-right: 3.125em;
    }
  }

  .header-auto{
    position: absolute;
    right: 1.875em;
    bottom: 0;
    &__show{
      width: 2.3125em;
      height: 2.3125em;
      padding: 0;
      justify-content: center;
      .header-auto__title{
        display: none;
      }
      .header-auto__img{
        margin: 0;
      }
    }
    &__hidden{
      left: unset;
      right: 0;
      width: 20em;
      padding-bottom: .875em;
    }
    &__title{
      font-size: .875em;
    }
    &__form{
      width: 100%;
      margin-top: .5em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .875em .625em;
    }
    &__btn{
      height: 2.0625em;
      padding: 0;
      width: 6.75em;
      margin: .875em 0 0 auto;
    }
  }

  .header-search{
    flex-grow: 1;
    display: grid;
    grid-template-columns: calc(100% - 2.3125em - .8125em) 2.3125em;
    justify-content: space-between;
    &__field{
      width: 100%;
      height: 2.3125em;
      input{
        height: 100%;
        font-size: .875em;
        padding: 0 .5em;
        border-radius: .35em;
      }
    }
    &__btn{
      padding: 0;
      margin: 0;
      width: 100%;
      height: 2.3125em;
      background-image: url(../img/search-icon.svg);
      @include bgImgSize(1.4375em);
      span{
        display: none;
      }
    }
    &__variants{
      width: calc(100vw - 3.75em);
    }
    &__variant{
      a{
        font-size: .875em;
      }
    }
  }
}


